export class User {
    UserID: number; // (用户ID)
    UserCode: string; // 用户账号
    Tel: string; // (联系方式)
    Password: string; // (密码)
    State: number; // (状态)
    StateName: string; // (状态名)
    WorkCode: string; // (人员编号)
    UserName: string; // (人员姓名)
    Post: string; // (岗位)
    RoleList: string; // （权限列表）
    Remark: string; // (备注)
}
